.title-form{
  font-size: 55px;
  text-align: center;
  margin: 100px 0 0;
}
.container-form{
  padding: 10px;
  max-width: 900px;
  margin: 30px auto;
  display: flex;
  flex-direction: column;
}

.container-form label{
  font-size: 20px;
}
.container-form input, .container-form textarea{
  padding: 10px;
  margin-bottom: 25px;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
}

.container-form button{
  padding: 10px 25px;
  align-self: flex-start;
  background: transparent;
  border-radius: 200px;
  border: 2px solid #9c0e9c;
  color: #9c0e9c;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}